html {
   line-height: 1.15;
   text-size-adjust: 100%;
   -webkit-tap-highlight-color: transparent;
   scroll-behavior: smooth;
}

body {
   margin: 0;
}

main {
   display: block;
}

h1 {
   margin: 0.67em 0;
   font-size: 2em;
}

hr {
   box-sizing: content-box;
   overflow: visible;
   height: 0;
}

pre {
   font-family: monospace;
   font-size: 1em;
}

a {
   background-color: transparent;
}

abbr[title] {
   border-bottom: none;
   text-decoration: underline dotted;
}

b,
strong {
   /* stylelint-disable-next-line font-weight-notation */
   font-weight: bolder;
}

code,
kbd,
samp {
   font-family: monospace;
   font-size: 1em;
}

small {
   font-size: 80%;
}

sub,
sup {
   position: relative;
   vertical-align: baseline;
   font-size: 75%;
   line-height: 0;
}

sub {
   bottom: -0.25em;
}

sup {
   top: -0.5em;
}

img {
   border-style: none;
}

button,
input,
optgroup,
select,
textarea {
   margin: 0;
   font-family: inherit;
   font-size: 100%;
   line-height: 1.15;
}

button,
input {
   overflow: visible;
}

button,
select {
   text-transform: none;
}

// noinspection CssInvalidPropertyValue
button,
[type="button"],
[type="reset"],
[type="submit"] {
   appearance: button;
}

button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
   border-style: none;
   padding: 0;
}

button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
   outline: 1px dotted ButtonText;
}

/** * Correct the padding in Firefox. */
fieldset {
   border: none;
   padding: 0;
}

legend {
   display: block;
   box-sizing: border-box;
   width: 100%;
   max-width: 100%;
   padding: 0;
   font-size: 1.5rem;
   line-height: inherit;
   white-space: normal;
   color: inherit;
}

progress {
   vertical-align: baseline;
}

textarea {
   overflow: auto;
}

[type="checkbox"],
[type="radio"] {
   box-sizing: border-box;
   padding: 0;
}

[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
   height: auto;
}

[type="search"] {
   appearance: textfield;
   outline-offset: -2px;
}

[type="search"]::-webkit-search-decoration {
   appearance: none;
}

// noinspection CssInvalidPropertyValue
::-webkit-file-upload-button {
   appearance: button;
   font: inherit;
}

details {
   display: block;
}

summary {
   display: list-item;
}

template {
   display: none;
}

[hidden] {
   display: none;
}
